import React from "react";
import "./About.css";
import { saveAs } from "file-saver";
import aboutImg from "./img/about.jpg";
function About() {
  //  Up To Top Btn
    window.addEventListener("scroll", function(){
      const upToTop = document.querySelector("a.bottom__to__top");
       upToTop.classList.toggle("active", window.scrollY > 0)
    });
  return (
    <div className="about component__space" id="About">
      <div className="container">
        <div className="row">
          <div className="col__2">
            <img src={aboutImg} alt="" className="about__img" />
          </div>
          <div className="col__2">
            <h1 className="about__heading">About Me</h1>
            <div className="about__meta">
              <h1 className="about__text p__color">Ivy Wanjiku, Software Developer</h1>
              <p className="about__text p__color">
                Dedicated to coming up with solutions that help shape the world to be better.</p>
              <p className="about__text p__color">
                Ivy specializes in a culture of continuous learning and growth. As a software developer, she has worked for a medium sized company in the technology sector. At ACIM, she was responsible for content creation of the ACIM website and development of their sister company Emmissary website. Her main focus is to offer solutions for competitor's clients.
              </p>
              <p className="about__text p__color">
                She is pursuing a Bachelor of Science in Applied Computer Technology from United States Iternational University - Africa. 
              </p>
              <p className="about__text p__color">
                Ivy enjoys meeting new people and learning about their lives and backgrounds. She easily finds common interests with strangers and tends to make most people feel comfortable. She finds this skill especially advantageous when kicking off projects with new clients.
              </p>
              <div className="about__button d__flex align__items__center">
                <a href="#">
                  <button className="about btn pointer">Download Cv</button>
                </a>
                <a href="#">
                  <button className="about btn pointer">Hire Me</button>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* UP TO TOP BTN */}
      <div className="up__to__top__btn">
        <a href="#" className="bottom__to__top">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="16"
          height="16"
          fill="currentColor"
          class="bi bi-chevron-up white"
          viewBox="0 0 16 16"
        >
          <path
            fill-rule="evenodd"
            d="M7.646 4.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1-.708.708L8 5.707l-5.646 5.647a.5.5 0 0 1-.708-.708l6-6z"
          />
        </svg>
        </a>
      </div>
    </div>
  );
}

export default About;